<template>
    <div>
        <el-row>
            <el-col>POST</el-col>
            <el-col>
                <el-switch v-model="download" inactive-text="结果保存为文件:"></el-switch>
            </el-col>
            <el-col>
                <el-input type="textarea" :autosize="{ minRows: 10, maxRows: 15}" v-model="postUrls"
                          placeholder="输入网址，例如，['http://xxx','','']">
                </el-input>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-button type="primary" @click="analysis">解析</el-button>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-form>
                    <el-form-item label="过滤条件(res.code，如果返回的结果中包含输入框中的值，就过滤)">
                        <el-input type="text" v-model="filter" size="small"></el-input>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <p>请求结果</p>
                <el-input type="textarea" :autosize="{ minRows: 10, maxRows: 15}" v-model="result">
                </el-input>
            </el-col>
        </el-row>
    </div>
</template>

<script>

    import http from "@/libs/http"
    import FileSaver from 'file-saver'

    export default {
        name: "api-post",
        data() {
            return {
                postUrls: "",
                download: false,
                result:"",
                filter: ""
            }
        },
        methods:{
            analysis() {
                let postArray = [];

                // console.log("post: ");
                // console.log(postArray);
                // let blob = new Blob([format.stringfy(postArray)], {type: "text/plain;charset=utf-8"});
                // FileSaver.saveAs(blob, "postUrls.txt");

                let postResultArray = [];
                let that = this;

                postArray = JSON.parse(this.$data.postUrls);
                (function postFun(index) {
                    if(index < postArray.length){
                        http.post(postArray[index]).then(res => {
                            if (that.filter.indexOf(res.code) === -1) {
                                postResultArray.push({url:postArray[index],res:res});
                            }
                            postFun(++index);
                        });
                    }
                    else {
                        if(that.download){
                            let blob = new Blob([JSON.stringfy(postResultArray)], {type: "text/plain;charset=utf-8"});
                            FileSaver.saveAs(blob,"postResult.txt");
                        }
                        else {
                            that.result = JSON.stringify(getResultArray,null, 2);
                        }
                    }
                })(0);

            }
        }
    }

</script>

<style lang="scss">
    .bg-purple-dark {
        background: #99a9bf;
    }

    .bg-purple {
        background: #d3dce6;
    }

    .bg-purple-light {
        background: #e5e9f2;
    }
</style>